export var VALID_HEX_LENGTH = 7;
export var validateHexCode = function (color) {
    // Cannot be blank
    if (!color || !color.length) {
        return 'Please enter a hexcode';
    }
    // Cannot contain spaces
    if (color.match(/^\s*$/)) {
        return 'Cannot contain spaces';
    }
    // Must be 7 characters including the #
    var isValidLength = color.length === VALID_HEX_LENGTH;
    if (!isValidLength) {
        return "Must be ".concat(VALID_HEX_LENGTH, " characters");
    }
    // Must begin with a # character
    var beginsWithHash = color.substring(0, 1) === '#';
    if (!beginsWithHash) {
        return 'Must begin with #';
    }
    // Cannot contain invalid characters
    var invalidChars = color.replace(/[abcdefABCDEF#0123456789]/g, '');
    if (invalidChars.length) {
        return 'Invalid hexcode';
    }
    // Can not have more than one # symbol
    var numberOfHashes = (color.match(/#/g) || []).length;
    if (numberOfHashes > 1) {
        return 'Can not contain more than one #';
    }
    // Valid hexcode exists
    return null;
};
export var invalidHexCharacters = /[^abcdefABCDEF#0123456789]/g;
