import { __spreadArray } from "tslib";
// @ts-ignore
import chroma from 'chroma-js';
// Constants
import { dropdownScrollColors, influxGradients } from '../Constants/colors';
export var getColorsFromGradient = function (gradient) {
    // @ts-ignore
    return dropdownScrollColors[gradient] || influxGradients[gradient];
};
export var generateInlineCSSGradient = function (angle, colors) {
    var angleText = "".concat(angle, "deg");
    var colorsText = colors.map(function (color) {
        return "".concat(color.color, " ").concat(color.position, "%");
    });
    var joinedProperties = __spreadArray([angleText], colorsText, true).join(', ');
    return { background: "linear-gradient(".concat(joinedProperties, ")") };
};
export var getAverageColorFromLinearGradient = function (linearGradient) {
    var rgbColors = linearGradient.match(/[rR][gG][bB][(]\d+[,]\s\d+[,]\s\d+[)]/g);
    // This function could fail if a linear gradient string is passed in using hexcodes
    // Didn't seem worth the time to make it more resilient since it is only
    // used within the button contrast tester widget
    if (rgbColors && rgbColors.length === 2) {
        var startColor = chroma(rgbColors[0]);
        var stopColor = chroma(rgbColors[1]);
        return "".concat(chroma.mix(startColor, stopColor));
    }
    return 'fail';
};
