import React from 'react';
import { createPortal } from 'react-dom';
var portalElementID = 'cf-portal';
var createPortalElement = function () {
    var portalElement = document.createElement('div');
    portalElement.setAttribute('class', portalElementID);
    portalElement.setAttribute('id', portalElementID);
    document.body.appendChild(portalElement);
    return portalElement;
};
var getPortalElement = function () {
    var portal = document.getElementById(portalElementID);
    if (!portal) {
        portal = createPortalElement();
    }
    return portal;
};
var createNotificationContainer = function (x, y) {
    var container = document.createElement('div');
    container.setAttribute('class', "cf-notification-container cf-notification__".concat(x, " cf-notification__").concat(y));
    container.setAttribute('id', "cf-notification-container-".concat(x, "-").concat(y));
    var portal = getPortalElement();
    portal.appendChild(container);
    return container;
};
var getNotificationContainer = function (x, y) {
    var container = document.getElementById("cf-notification-container-".concat(x, "-").concat(y));
    if (!container) {
        container = createNotificationContainer(x, y);
    }
    return container;
};
export var usePortal = function () {
    var portal = getPortalElement();
    var addElementToPortal = function (element, forceToTop) {
        var registeredElement = element;
        if (forceToTop) {
            registeredElement = React.createElement("div", { style: { zIndex: 9999 } }, element);
        }
        return createPortal(registeredElement, portal);
    };
    var addNotificationToPortal = function (element, x, y) {
        var container = getNotificationContainer(x, y);
        return createPortal(element, container);
    };
    var addEventListenerToPortal = function (eventType, func) {
        portal.addEventListener(eventType, func);
    };
    var removeEventListenerFromPortal = function (eventType, func) {
        portal.removeEventListener(eventType, func);
    };
    return {
        addElementToPortal: addElementToPortal,
        addNotificationToPortal: addNotificationToPortal,
        addEventListenerToPortal: addEventListenerToPortal,
        removeEventListenerFromPortal: removeEventListenerFromPortal,
    };
};
